.main-list {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 0 20px;
	.main-category {
		width: 100%;
		max-width: 1900px;
		margin: 20px 50px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		gap: 10px;
		.category-title {
			width: 100%;
			margin: 0 auto;
			padding-left: 3px;

			a {
				color: white;
				transition: 0.1s ease-in;
				&:hover {
					font-weight: 500;
				}
			}
		}
		.category-list-container {
			position: relative;
			width: 100%;
			transition: 0.3s ease-in;
			margin: 0 auto;
			.category-back {
				left: 0;

				&:hover {
					-webkit-box-shadow: inset 60px 0 25px -15px rgba(216, 216, 216, 0.97);
					-moz-box-shadow: inset 60px 0 25px -15px rgba(216, 216, 216, 0.97);
					box-shadow: inset 60px 0 25px -15px rgba(216, 216, 216, 0.97);
					opacity: 1;
				}
			}
			.category-forward {
				right: 0;
				&:hover {
					-webkit-box-shadow: inset -60px 0 25px -15px rgba(216, 216, 216, 0.97);
					-moz-box-shadow: inset -60px 0 25px -15px rgba(216, 216, 216, 0.97);
					box-shadow: inset -60px 0 25px -15px rgba(216, 216, 216, 0.97);
					opacity: 1;
				}
			}

			.category-list {
				display: flex;
				.category-movie {
					min-width: 110px;
					height: 170px;
					display: flex;
					justify-content: center;
					align-items: center;
					transition: 0.3s ease-in;
					&:hover {
						img {
							transform: scale(1.1);
							transform: scale(1.1);
							-webkit-box-shadow: -30px 0px 40px -15px black, 30px 0px 40px -15px black;
							-moz-box-shadow: -30px 0px 40px -15px black, 30px 0px 40px -15px black;
							box-shadow: -30px 0px 40px -15px black, 30px 0px 40px -15px black;
							z-index: 9999;
						}
					}
					.category-movie-poster {
						width: 100px;
						transition: 0.1s ease-in;
						border-radius: 5px;
						&.have-not {
							filter: grayscale(0.8);
						}
						&.loading {
							display: none;
						}
					}
				}
			}
		}
	}
}

.category-navigate {
	fill: white;
	background-color: transparent;
	position: absolute;
	height: 150px;
	width: 80px;
	display: flex;
	align-items: center;
	justify-content: center;
	top: 50%;
	transform: translateY(-50%);
	transition: 0.1s ease-in;
	z-index: 999;
	border-radius: 5px 0 0 5px;
	opacity: 0;
}

.swiper-button-disabled {
	display: none;
}
